<template>
  <div class="card">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Manage Item</span> &nbsp;
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--back"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewALl"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body" style="min-height: 300px;">
      <div>
        <ACCFormList
          :prop="propList"
          :title="''"
          @rowClicked="rowClicked"
          ref="ref_ManageItem_List"
          @onRenderData="onRenderData"
        >
        </ACCFormList>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/MM/MM_Sparepart",
        LineNo: 0,
        initialWhere:
          "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
        PerPage: 6,
      },
    };
  },
  computed: {},
  methods: {
    doViewALl() {
      this.$router.push({ name: "MM_ManageItem" });
    },
    rowClicked(record, index) {
      var param = record;
      param.isEdit = false;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MM_ManageItemView" });
    },

    RenderData() {
      this.$refs.ref_ManageItem_List.doGetList("");
    },
  },
  mounted() {
    this.RenderData();
  },
};
</script>

<style scoped>
.card {
  border: 0px solid #e7eaec !important;
}
.card-header:first-child {
  background-color: #eeeeee !important;
}

.tab-content > .active {
  padding: 0px !important;
}

.isTabs {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  background-color: #999999;
  width: 81px;
  text-align: center;
  height: 30px;
  padding-top: 0.3%;
  margin-right: 5px;
}

.active {
  background-color: #333399 !important;
}
</style>